.footer {
  margin-bottom: 81px;

  font-family: $ff-montserrat;

  .container {
    display: flex;
    justify-content: space-between;

    .footer__wrapper {
      display: flex;
      flex-direction: column;

      border-right: solid 2px #D7D9DF;

      .footer__title {
        margin: 0;
        margin-bottom: 20px;

        font-weight: 600;
        font-size: 18px;
      }

      .footer__li-tours {
        columns: 3;
        column-gap: 50px;


        list-style-type: none;
        font-size: 14px;
        color: $color-default-products;
        opacity: 70%;
      }

      .footer__email {
        display: flex;
        gap: 15px;

        font-size: 14px;
        color: $color-default-orange;

        .text-tablet {
          display: none;
        }
      }

      .footer__phone {
        display: flex;
        align-items: flex-end;
        gap: 11px;

        font-size: 16px;
        color: $color-default-orange;
        text-decoration: underline;
      }

      .footer__desc {
        margin: 0;
        max-width: 320px;

        font-size: 14px;
        color: $color-default-products;
        opacity: 50%;
      }
    }

    .footer__wrapper:last-child {
      border: none;
    }

    .footer__next-tours {
      padding-right: 39px;
    }

    .footer__contacts {
      padding-right: 33px;
    }

  }
}

@media (max-width: 1200px) {
  .footer {
    margin-bottom: 50px;

    .container {
      flex-wrap: wrap;
      justify-content: center;
      column-gap: 50px;

      .footer__wrapper {
        margin-bottom: 30px;



        .footer__li-tours {
          column-gap: 30px;
        }

        .footer__email {
          .text {
            display: none;
          }

          .text-tablet {
            display: block;
          }
        }
      }

      .footer__wrapper:last-child {
        min-width: 726px;

        align-items: center;
        margin-bottom: 0px;
        padding-top: 30px;

        border-top: solid 2px #D7D9DF;
        text-align: center;

      }

      .footer__contacts {
        padding-right: 0;

        border: none;
      }

    }
  }
}

@media (max-width: 767px) {
  .footer {
    margin-bottom: 25px;
    .container {
      .footer__wrapper {
        min-width: 288px;

        display: flex;
        align-items: center;
        margin-bottom: 30px;
        
        border: none;
        border-bottom: 1px solid $color-white-border;
        padding: 0;
        padding-bottom: 20px;

        .footer__title {
          line-height: 32px;
        }

        .footer__li-tours {
          max-width: 288px;

          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          align-items: flex-start;
          justify-content: center;
          text-align: center;
          row-gap: 20px;
        }

        .footer__email {
          margin-bottom: 15px;
        }
        .footer__desc{
          max-width: 288px;
        }
      }

      .footer__wrapper:last-child {
        border-top: none;

        margin-bottom: 0;
        padding: 0;
      }

      .footer__contacts {

      }

    }
  }
}